.nav {
    top: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    height: 50px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
}


.logo {
    width: 80px;
    height: 50px;
    margin-left: 20px;
    margin-top: 2px;
    object-fit: contain;
}

.avatar {
    width: 30px;
    height: 30px;
    object-fit: contain;
    margin-right: 10px;
    margin-top: 10px;
}

.nav__black {
    background-color: #111;
}