.row {
    margin-left: 10px;
    color: white;
}

.row h2 {
    margin-left: 15px;
}

.row_Line {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row_Line::-webkit-scrollbar {
    display: none;
}

.row_Lines {
    object-fit: contain;
    width: 100%;
    max-height: 100px;
    transition: transform 400ms;
    margin-right: 10px;
}

.row_Lines:hover {
    transform: scale(1.1);
}

.Big_Row {
    max-height: 250px;
}

.youtube {
    display: block;
    margin: 0 auto;
}

[class*="col-"] {
    width: 100%;
}

@media only screen and (min-width: 600px) {

    /* For tablets: */
    .col-s-1 {
        width: 8.33%;
    }

    .col-s-2 {
        width: 16.66%;
    }

    .col-s-3 {
        width: 25%;
    }

    .col-s-4 {
        width: 33.33%;
    }

    .col-s-5 {
        width: 41.66%;
    }

    .col-s-6 {
        width: 50%;
    }

    .col-s-7 {
        width: 58.33%;
    }

    .col-s-8 {
        width: 66.66%;
    }

    .col-s-9 {
        width: 75%;
    }

    .col-s-10 {
        width: 83.33%;
    }

    .col-s-11 {
        width: 91.66%;
    }

    .col-s-12 {
        width: 100%;
    }
}

